@import url(https://fonts.googleapis.com/css?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Home {
  text-align: center;
}

.Home-logo {
  height: 20vmin;
}

.Home-header {
  background-color: #323639;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Home-link {
  color: #09d3ac;
}

.jumbotron {
  background-color: white;
}

.welcome {
  margin-top: 20rem;
  text-shadow: 0px 0px 10px black, 1px 1px 1em black, 0 0 0.2em black;
}

.welcome-text {
  text-shadow: 0px 0px 10px black, 1px 1px 1em black, 0 0 0.2em black;
  font-size: x-large;
  font-weight: 550;
}

button {
  box-shadow: 0px 0px 10px black, 1px 1px 1em black, 0 0 0.2em black;
}

.in-text-link:hover {
  color: #0056b3;
}
.menu {
    color: rgba(255, 255, 255, .5);
    text-decoration: none;
}

.icon {
    display: none;
}

a:link, a:visited {
    text-decoration: none;
}

a:hover, a:active, .menu-active {
    color: rgba(255, 255, 255, 1);
}

@media screen and (max-width: 570px) {
    .nav-link {
        display: none;
    }
    .icon {
        display: block;
    }
}
.timeline {
  font-size: 20px;
  width: 85%;
  height: 125px;
  margin: auto;
  margin-top: 30px;
}

.event {
  text-align: center;
  margin-left: 15%;
  margin-right: 15%;
}

h1 {
  text-align: center;
  margin-top: 80px;
  font-family: 'Bebas Neue', cursive;
  font-size: 4.5em;
}

h2 {
  font-weight: 600;
  color: #2E57A1;
}

.logo {
  width: 50vmin;
  padding: 1%;
  margin-bottom: 20px;
}
.card-body {
  font-family: 'Oswald', sans-serif;
}
body {
  font-family: 'Roboto', sans-serif;
  line-height: 2em;
}

p {

}

.App {
  text-align: center;
}

.App-logo {
  height: 20vmin;
}

.App-header {
  background-color: #323639;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
